<template>
  <div class="index-wrap">
    <form action="/">
      <van-search
        v-model="value"
        show-action
        placeholder="请输入搜索关键词"
        shape="round"
        @search="onSearch"
        @cancel="onCancel"
      />
    </form>
    <van-row>
      <van-col span="5">
        <van-sidebar v-model="activeKey">
          <van-sidebar-item 
            :title="item.name"
            v-for="(item, index) in sideBars" 
            :key="index"
          />
        </van-sidebar>
      </van-col>
      <van-col span="19">
        <van-grid>
          <van-grid-item
            v-for="(item, index) in leftSection.slice(0, 7)" 
            :key="index"
            :text="item.name"
            :class="item.id === activeClass ? 'activeClass' : ''"
          >
            <van-image :src="item.url" @click="showClass(item)" />
          </van-grid-item>
          <van-grid-item>
            <van-image :src="moreImg" @click="showPopup"/>
          </van-grid-item>
        </van-grid>
        <van-divider>券列表</van-divider>
        <van-list>
          <van-cell 
            v-for="(item, index) in list" 
            :key="index" 
            class="list"
            :class="item.sell === 0 ? 'disabled': ''"
            @click="goParchase(item)"
          >
            <div class="title">{{item.title}}</div>
            <div class="desc">
              <span :class="['words', spreadArray[index] === false ? 'more' : '']">
                {{item.desc}}
              </span>
              <van-icon
                class="spread"
                v-if="spreadArray[index] === true"
                name="ellipsis" 
                size="20"
                @click="spreadDesc(index)"
              />
            </div>
            <div>
              <span class="fl price">¥{{item.price}}</span>
              <van-icon 
                class="fr" 
                v-if="item.star === true"
                name="star-o" 
                color="#07c160"
                size="20"
              />
              <van-icon 
                class="fr" 
                v-else
                name="star" 
                color="#07c160"
                size="20"
              />
            </div>
          </van-cell>
        </van-list>
      </van-col>
    </van-row>
    <van-popup
      v-model="show"
      closeable
      close-icon="close"
      close-icon-position="bottom-right"
      position="top"
      :style="{ height: '70%' }"
    >
      <van-grid>
        <van-grid-item
          v-for="(item, index) in leftSection" 
          :key="index"
          :text="item.name"
          :class="item.id === activeClass ? 'activeClass' : ''"
          @click="searchAndHidePopup(item)"
        >
          <van-image :src="item.url" />
        </van-grid-item>
      </van-grid>
    </van-popup>
    <TabBar />
  </div>
</template>

<script>
import TabBar from '@/components/TabBar'

export default {
  name: 'index',
  components: {
    TabBar
  },
  data () {
    return {
      value: '',
      activeKey: 0,
      activeClass: 0,
      error: false,
      show: false,
      loading: false,
      spreadArray: [],
      moreImg: require('../assets/images/7d71a329.more.png'),
      sideBars: [
        { name: '收藏', id: 10 },
        { name: '热门', id: 0 },
        { name: '新品', id: 1 },
        { name: '商超购物', id: 2 },
        { name: '影视下载', id: 3 },
        { name: '游戏点卡', id: 4 },
        { name: '甜品饮品', id: 5 },
        { name: '生活服务', id: 6 },
        { name: '美食生鲜', id: 7 },
        { name: '运动出行', id: 8 },
        { name: '音乐读书', id: 9 },
      ],
      leftSection: [
        { name: '美食视频通兑券鲜', id: 0, url: 'http://image.99shou.cn/self/coupons/logos/爱奇艺.png!img' },
        { name: '爱奇艺视频', id: 1, url: 'http://image.99shou.cn/self/coupons/logos/爱奇艺.png!img' },
        { name: '票神', id: 2, url: 'http://image.99shou.cn/self/coupons/logos/票神.png!img' },
        { name: '腾讯视频', id: 3, url: 'http://image.99shou.cn/self/coupons/logos/腾讯.png!img' },
        { name: '百度网盘', id: 4, url: 'http://image.99shou.cn/self/coupons/logos/百度网盘.png!img' },
        { name: '咪咕阅读', id: 5, url: 'http://image.99shou.cn/self/coupons/logos/爱奇艺.png!img' },
        { name: '芒果视频', id: 6, url: 'http://image.99shou.cn/self/coupons/logos/爱奇艺.png!img' },
        { name: '美食视频通兑券鲜', id: 7, url: 'http://image.99shou.cn/self/coupons/logos/爱奇艺.png!img' },
        { name: '美食视频通兑券鲜', id: 8, url: 'http://image.99shou.cn/self/coupons/logos/爱奇艺.png!img' },
        { name: '美食视频通兑券鲜', id: 9, url: 'http://image.99shou.cn/self/coupons/logos/爱奇艺.png!img' },
        { name: '美食视频通兑券鲜', id: 10, url: 'http://image.99shou.cn/self/coupons/logos/爱奇艺.png!img' },
        { name: '美食视频通兑券鲜', id: 11, url: 'http://image.99shou.cn/self/coupons/logos/爱奇艺.png!img' },
      ],
      list: []
    }
  },
  computed: {
  },
  created () {
    this.initList()
  },
  methods: {
    showClass (item) {
      this.activeClass = item.id
    },
    showPopup () {
      this.show = true
    },
    searchAndHidePopup (item) {
      this.activeClass = item.id
      this.show = false
    },
    initList () {
      let list = [
        { 
          title: '25收百果园30代金券',
          desc:'银联*平安*光大*平安RUN、每日10点光大银行15买30平安RUN、每日10点光大银行15买30平安RUN、每日10点光大银行15买30', 
          id: 1,
          price: 25,
          star: true,
          sell: 0
        },
        { 
          title: '25收百果园30代金券',
          desc: '银联*平安*光大*平安RUN', 
          price: 25,
          id: 2 
        },
        { 
          title: '25收百果园30代金券',
          desc:'银联*平安*光大', 
          price: 25,
          id: 3,
          sell: 0
        },
        { title: '25收百果园30代金券',
          desc:'银联*平安*光大*平安RUN、每日10点光大银行15买30', 
          price: 25,
          id: 4
        },
        { title: '25收百果园30代金券',
          desc:'银联*平安*光大*平安RUN、每日10点光大银行15买30', 
          price: 25,
          id: 5
        }
      ]

      this.list = list

      for (let i = 0; i < list.length; i++) {
        const el = list[i]

        if (el.desc.length >= 20) {
          this.spreadArray[i] = true
        } else {
          this.spreadArray[i] = false
        }
      }
    },
    goParchase (item) {
      this.$router.push({ path: '/parchase', query: {id: item.id} })
    },
    onSearch (val) {
      this.$toast(val)
    },
    onCancel () {
      this.$toast('取消')
    },
    spreadDesc (index) {
      this.spreadArray[index] = false
    }
  }
}
</script>



